.btn{
   padding: 11px 16px;
   padding: 9px 16px\9;
   line-height: 20px;
   font-size: 14px;
   text-align: center;
   color: #fff;
   background: #3c9dea;     
   background: -webkit-linear-gradient(bottom, #3892da, #3c9dea);   
   background: -moz-linear-gradient(bottom, #3892da, #3c9dea);    
   background: -o-linear-gradient(bottom, #3892da, #3c9dea);   
   background: -ms-linear-gradient(bottom, #3892da, #3c9dea);
   border: none;
   box-sizing: border-box;
   display: inline-block;
}
.btn-sm{
    padding: 6px 10px;
    font-size: 12px;
}
.btn-large{
   padding: 16px 20px;
   font-size: 16px;
}
.btn-block{
   width: 100%;
   display: block;
}
.orange-btn{
    background: #f15a29;
}
