@import "base/_common";
@import "nav";
@import "pagination";

//置底
html,body{
    height: 100%;
    margin: 0;
}
.wrapper{
    min-height: 100%;
    margin-bottom: -71px;
}
.push{
    height: 71px;
}
.container{
    width: 1000px;
    margin: 0 auto;
    box-sizing: border-box;
}
.index-container{
    margin-left:201px ;
    overflow-x: hidden;
}
.header,.footer{
    padding: 40px 0;
}
.blue-bg{
    position: relative;
    z-index: 3;
    background: -webkit-gradient(linear, 0 0, 0 100%, from(#59aae9), to(#2b8ad4));   
    background: -webkit-linear-gradient(top, #59aae9, #2b8ad4);   
    background: -moz-linear-gradient(top, #59aae9, #2b8ad4);   
    background: -o-linear-gradient(top, #59aae9, #2b8ad4);   
    background: -ms-linear-gradient(top, #59aae9, #2b8ad4);   
    background: linear-gradient(top, #59aae9, #2b8ad4);   
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#59aae9', endColorstr='#2b8ad4',GradientType=0 ); 
}
.index-header{
    background: url(../img/header-bg.png) no-repeat left center;
    padding: 16px 40px;
    color: #fff;
    position: relative;
    .header-info,.line-gap,a{
        display: inline-block;
        vertical-align: middle;
        color: #fff;
    }
    .line-gap{
        background: url(../img/line.png) no-repeat center center;
        width: 80px;
        height: 60px;
    }
    i{
        font-size: 40px;
        vertical-align: middle;
    }
    
}
.index-footer{
    border-top:1px solid #ccc ;
    background: #FFF;
    padding: 25px 40px;
    position: relative;
    z-index: 3;
}
.copyright{
    color: #666;
}
.login-bg{
    background: url(../img/bg.jpg) center top no-repeat;
    background-size:cover ;
    min-height: 660px;
}
.login-box{
    width: 370px;
    float: right;
    border: 1px solid #fff;
    background: rgba(255,255,255,.8);
    box-shadow: inset 0 0 1px #4693c8;
    background: #fefefe \9;
    margin-top:80px ;
    padding: 50px 40px;
    box-sizing: border-box;
}
.login-title{
    color: #176cbb;
    text-shadow: 4px 0 5px rgba(94,153,210,.45);
    padding-bottom: 20px;
    border-bottom: 1px solid #176CBB;
}
.login-input{
    position: relative;
    input{
        padding-left:60px ;
        font-size: 16px;
        color: #888;
    }
    input:focus~ .icon{color: #176CBB; border-color:#176CBB ;}
    input:focus{ color: #176CBB}
    .icon{
        position: absolute;
        font-size: 25px;
        left: 10px;
        top: 8px;
        padding-right: 10px;
        border-right: 1px solid #ccc;
        color: #ccc;
        line-height: 1;
        display: inline-block;
        width: 25px;
        text-align: center;
    }
    
}

.desktop-chart{
    display: inline-block;
    vertical-align: top;
    position: relative;
    .icon-bar-chart{
        position: absolute;
        top: -3px;
        left: 4px;
        font-size: 9px;
        margin: 0;
        width: inherit;
        z-index: 1;
    }
}
.index-search{
    padding: 60px;
}
.search-box{
   
    input[type="text"]{
        width: 500px;
        border-radius:0 ;
    }
    .btn{
        width: 80px;
        border-radius:2px ;
        margin-left: 10px;
    }
}
.avatar{
    border: 1px solid #ccc;
    border-radius:4px ;
    margin-right: 10px;
}
.analysis-info{
    background: #3892da;
    color: #fff;
    padding:10px 20px;
    b{
        font-size: 120%;
    }
}
#chart{
    padding: 20px;
}


