@charset "UTF-8";
/*CSS Reset*/
html { -ms-text-size-adjust: 100%; -webkit-text-size-adjust: 100%;min-width: 1000px;}
body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, legend, input, textarea, blockquote, th, td, header, hgroup, nav, section, article, aside, footer, figure, figcaption, menu, button {
  margin: 0;
  padding: 0;
}
p{margin: 0 0 10px;}
img{
  max-width: 100%;
  vertical-align: middle;
}
header, section, footer { display: block }
body {
  font-size: 14px;
  line-height: 20px;
  font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;;
  color: #333;
  -webkit-user-select: none;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
  outline: 0;
  -webkit-overflow-scrolling: touch;
  min-height:100%;
}
h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: normal;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}
caption, th {
  text-align: left;
}
fieldset, img {
  border: 0;
}
li {
  list-style: none;
}
ins {
  text-decoration: none;
}
del {
  text-decoration: line-through;
}
input, button, textarea, select, optgroup, option {
  font-family: inherit;
  font-size: inherit;
  font-style: inherit;
  font-weight: inherit;
  outline: 0;
}
button {
  -webkit-appearance: none;
  border: 0;
  background: none;
}
a {
  -webkit-touch-callout: none;
  text-decoration: none;
  color: #176CBB;
}
:focus {
  outline: 0;
  -webkit-tap-highlight-color: transparent;
}
em, i {
  font-style: normal;
}
a,
input,
textarea,
select,
button {
  outline: 0;
}

