@import "base/_variable";
.list-table{
    width: 100%;
    th{
        @include white-bg;
        font-weight: normal;
    }
    td,th{
        border: 1px solid #ccc;
        text-align: center;
        
        vertical-align: middle;
    }
    td{
       padding: 6px 10px;
    }
    th{
        padding: 10px;
        border: 2px solid #ccc\9;
    }
}
.people-info-table{
    margin: 20px;
    td{padding:10px 15px;}
    i{
        font-size: 30px;
        color: #5cb85c;
    }
}
