@import "variable","rest","table","form","button";
.border-bottom{
    border-bottom: 1px solid #dedede;
    padding-bottom: 30px;
}

.gap{
    padding: 20px;
}
.gap-lr{
    padding:0 20px;
}
.nogap{
    padding: 0;
}

.h1,h1{
    font-size: 30px;
    line-height: 1.5;
}
.h2,h2{
    font-size: 24px;
    line-height: 1.5;
}
.h3,h3{
    font-size: 18px;
    line-height: 1.5;
}
.h4,h4{
    font-size: 16px;
    line-height: 1.5;
}

.pull-left{
    float: left;
}
.pull-right{
    float: right;
}

.text-center{
    text-align: center;
}
.text-right{
    text-align: right;
}
.text-left{
    text-align: left;
}
.text-cut{
    overflow: hidden;
    white-space:nowrap;
    text-overflow: ellipsis;
}

.border-box{
    border: 1px solid #ccc;
    border-radius:4px ;
}

.gray-color{
    color: #999;
}
