.pagination{
    ul{
        display: inline-block;
        border-right:1px solid #ccc ;
        >li{
            display: inline;
            >a,>span{
                float: left;
                padding: 6px 15px;
                line-height: 20px;
                text-decoration: none;
                background-color: #fff;
                border: 1px solid #ccc;
                border-right:0 ;
            }
            &:hover > a,&:hover > span{
                background: #3892DA;
                color: #fff;
            }
            &.active > a,&.active > span{
                background: #176CBB !important;
                color: #fff;
                cursor: default;
            }
            &.disable > a,&.disable > span{
                background: none !important;
                color: #999 !important;
                cursor: default;
            }
            &:first-child > a,&:first-child > span{
                border-right:0 ;
            }
        }
    }
}
