
input[type="text"],
input[type="password"],
input[type="tel"],
input[type="number"],
input[type="submit"],input[type="reset"],
select,button{
    box-sizing: border-box;
    padding: 10px 12px;
    font-size: 14px;
    line-height: 20px;
    border-radius: 4px;        
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    box-shadow: none;
    border: 1px solid #ccc;
    vertical-align: middle;
    
    &:focus{
        border-color:#176cbb;
    }
}
input[type="checkbox"]{
    vertical-align: middle;
}
.input-block{
    width: 100%;
}
